	import appconfig from 'utils/config.js'
    import {mapState,mapMutations} from 'vuex'
    export default {
        computed: {
            ...mapState(['hasLogin', 'userInfo','system_config'])
        },
		onShow:function(){

		},
        methods: {
            ...mapMutations(['login']),
            toLogin() {
                uni.navigateTo({
                    url: '../account/login',
                });
            },
			toMyQuestion(uid){
				
				if(!uid || uid < 1){
					this.toLogin()
					return false;
				}
                uni.navigateTo({
                    url: 'question?uid='+uid
                });
			},
			toMyArticle(uid){
				if(!uid || uid < 1){
					this.toLogin()
					return false;
				}				
                uni.navigateTo({
                    url: 'article?uid='+uid
                });
			},
			toMyFollows(uid){
				if(!uid || uid < 1){
					this.toLogin()
					return false;
				}				
                uni.navigateTo({
                    url: 'follows?uid='+uid
                });
			},
			topeople(uid){
				if(!uid || uid < 1){
					this.toLogin()
					return false;
				}
				uni.navigateTo({
					url:'../people/people?uid='+uid
				})
			},
			toSetting(){
				if(!this.userInfo.uid || this.userInfo.uid < 1 || !this.hasLogin){
					this.toLogin()
					return false;
				}				
				uni.navigateTo({
					url:'/pages/user/setting'
				})	
			},
			showVersion(){
				uni.showToast({
					icon:'none',
					title:'版本号：'+appconfig.VERSION
				})
			}
						
        }
    }
